
import { Showcase } from "./Showcase";
import { Slides } from "./Slides";
import { Cursor } from "./Cursor";
import image1 from "../images/1.png";
import image2 from "../images/2.png";
import image3 from "../images/3.png";
import image4 from "../images/4.png";
import image5 from "../images/5.png";





const container = document.getElementById("app");
const cursor = new Cursor(document.querySelector(".cursor"));
const slidesData = [
{
image: image1,
title: "ABOUT",
meta: "ROBERT HINDS",
more: "https://www.humannothuman.ai/vid-04",
hasThevron: true // No chevron animation for this slide
},
{
image: image2,
title: "PROJECTS",
meta: "WEB DEVELOPMENT",
more: "https://www.humannothuman.ai/projects",
hasChevron: true,
hasThevron: true 

},
{
image: image3,
title: "SHOP",
meta: "ART |  PRINTS | CLOTHING",
more: "https://www.humannothuman.ai/shop",
hasChevron: true,
hasThevron: true 
},
{
image: image4,
title: "CONTACT",
meta: "email",
more: "https://www.humannothuman.ai/contact",
hasChevron: true,
hasThevron: true 
},
{
image: image5,
title: "PLAYGROUND",
meta: "ALL FUN",
more: "https://www.humannothuman.ai/playground",

hasChevron: true 
}
];

const slides = new Slides(slidesData);
const showcase = new Showcase(slidesData, {
  onActiveIndexChange: activeIndex => {
    slides.onActiveIndexChange(activeIndex);
  },
  onIndexChange: index => {
    slides.onMove(index);
  },
  onZoomOutStart: ({ activeIndex }) => {
    cursor.enter();
    slides.appear();
  },
  onZoomOutFinish: ({ activeIndex }) => {},
  onFullscreenStart: ({ activeIndex }) => {
    cursor.leave();
    slides.disperse(activeIndex);
  },
  onFullscreenFinish: ({ activeIndex }) => {}
});

showcase.mount(container);
slides.mount(container);
showcase.render();

window.addEventListener("resize", function() {
  showcase.onResize();
});

window.addEventListener("mousemove", function(ev) {
  showcase.onMouseMove(ev);
});
